<template>
  <router-view></router-view>
  <el-container>
    <el-header>Optimus可视化系统</el-header>
    <el-main style="border: 2px">
      <el-row>
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="医院编号">
            <el-select
                v-model="hospitalNo"
                clearable
                filterable
                allow-create
                default-first-option
                placeholder="请选择医院编号">
              <el-option v-for="item in options"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="流水号">
            <el-input v-model="admissionId" placeholder="请输入流水号" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="queryList">查询</el-button>
          </el-form-item>
        </el-form>
      </el-row>
      <el-row>
        <el-table :data="tableData" border style="width: 100%">
          <el-table-column fixed type="index" label="序号" width="60">
          </el-table-column>
          <el-table-column
              prop="admissionId"
              label="流水号">
          </el-table-column>
          <el-table-column
              prop="hospitalName"
              label="所属医院">
          </el-table-column>
          <el-table-column
              prop="stage"
              label="病历阶段">
          </el-table-column>
          <el-table-column
              fixed="right"
              label="操作">
            <template v-slot="scope">
              <el-button @click="reUnderstand(scope.row.admissionId)" type="danger">重新理解</el-button>
              <el-button @click="docUnderstandResult(scope.row)" type="success">病历详情</el-button>
<!--              <el-button @click="patientModelResult(scope.row.num)" type="warning">患者模型理解结果</el-button>-->
            </template>
          </el-table-column>
        </el-table>
      </el-row>
    </el-main>
    <el-footer>
      <div class="block">
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page="pagination.currentPage"
            :page-size="pagination.pageSize"
            layout="->,total, sizes, prev, pager, next, jumper"
            :total="pagination.total"
            :page-sizes="[10,50,100,200,300,500]"
            @size-change="handleSizeChange">
        </el-pagination>
      </div>
    </el-footer>
  </el-container>
</template>
<script>
  import {getRecordList} from "../apis/get";
  import {getHospitalDropDown} from "../apis/get"
  // import {} from "../apis/post";
  export default {
    data() {
      return {
        tableData: [],
        options: [],
        admissionId:'',
        hospitalNo:'',
        pagination: {
          currentPage: null,
          pageSize: null,
          total: null
        }
      }
    },
    methods:{
      //获取医院下拉框信息
      hospitalDropDown(){
        var _this = this;
        getHospitalDropDown().then(function (response){
          console.log(response);
          _this.options=response.data.data;
        })
      },
      //查询列表
      queryList(){
        var _this = this;
        // alert('ho:'+this.hospitalNo);
        // alert('ad:'+this.admissionId);
        getRecordList(_this.hospitalNo,_this.admissionId,_this.pagination.pageSize,_this.pagination.currentPage).then(function (response){
          _this.tableData=response.data.data.records;
          _this.pagination.pageSize=response.data.data.size;
          _this.pagination.currentPage=response.data.data.current;
          _this.pagination.total=response.data.data.total;
        })
      },
      reUnderstand(row) {
        alert(row);
        console.log(row);
      },
      //跳转到文书列表MedicalDocList页面
      docUnderstandResult(row){
        // alert(row.hospitalId);
        // alert(row.admissionId);
        // alert(row.stage);
        this.$router.push('/MedicalDocList/'+row.hospitalId+'/'+row.admissionId+'/'+row.stage);
      },
      //分页插件方法
      //切换当前页
      handleCurrentChange(val) {
        // alert(val);
        this.pagination.currentPage = val;
        this.queryList();
      },
      //切换每页显示条数
      handleSizeChange(val) {
        // alert(`每页 ${val} 条`);
        this.pagination.pageSize = val;
        this.queryList();
      },

    },
    mounted() {
      this.hospitalDropDown();
      this.queryList();
    }
  }
</script>
<style>
.el-header{
  background-color: #409EFF;
  color: #ffffff;
  text-align: left;
  line-height: 60px;
  font-size: 20px;
  //font-family:"微软雅黑";
}
.el-footer {
  line-height: 60px;
}
.el-main {
}
</style>